#peopleBackground {
  margin-bottom: 20vh;
}

#privacyTop {
  /* position: absolute; */
  /* top: 0; */
  /* left: 0; */
  width: 100%;
  padding: 5vh 4vh 3vh;
  text-align: center;
  box-shadow: 1px 2px 5px #757579;
}

#aboutUs {
  font-size: 100%;
  margin-bottom: 3vh;
  text-align: left;
  font-family: "Codystar", cursive;
  font-weight: 900;
  display: inline-block;
  padding: 5px 10px;
}

#aboutUsBreak {
  display: block;
}

#peoplePicBox {
  padding: 30px;
  text-align: center;
}

#peoplePicBox::after {
  content: "";
  display: block;
  clear: both;
}


.person:hover {
  background-color: #b2d08b;
}

#loginBackground {
  height: 200vh;
}

#locations {
  width: 100vw;
  display: none;
}

#blogButton {
  display: flex;
  justify-content: center;
  align-items: center;
}

#blogButtonWords {
  padding: 0.5em;
  background-color: #b2d08d;
  border: 2px solid #b2d08d;
  color: #f4f7fc;
  border-radius: 5px;
}

#blogButtonWords:hover {
  background-color: #f4f7fc;
  color: #b2d08d;
}
