.cross {
    position: relative;
    display: inline-block;
}
.cross::before, .cross::after {
    content: '';
    width: 100%;
    position: absolute;
    right: 0;
    top: 50%;
}
.cross::after {
    border-bottom: 4px solid red;
    width: 40px;
    -webkit-transform: skewY(10deg);
    transform: skewY(30deg) translateY(8px) translateX(-5px);
}
